import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import Quagga, { QuaggaJSConfigObject, QuaggaJSResultObject } from '@ericblade/quagga2';
import { defaultsDeep } from 'lodash';
import { BehaviorSubject, Observable } from 'rxjs';

const DEFAULT_CONFIG: QuaggaJSConfigObject = {
  inputStream: {
    name: 'Live',
    type: 'LiveStream',
    target: null,
    constraints: {
      width: { min: 640 },
      height: { min: 480 },
      aspectRatio: { min: 1, max: 2 },
      facingMode: 'environment', // or user
    },
    singleChannel: false // true: only the red color-channel is read
  },
  locator: {
    patchSize: 'medium',
    halfSample: true
  },
  locate: true,
  numOfWorkers: 4,
  decoder: {
    readers: ['code_128_reader']
  }
};

export enum BARCODE_TYPE {
  code_128 = 'code_128',
  code_39 = 'code_39',
  code_39_vin = 'code_39_vin',
  ean = 'ean',
  ean_extended = 'ean_extended',
  ean_8 = 'ean_8',
  upc = 'upc',
  upc_e = 'upc_e',
  codabar = 'codabar',
  i2of5 = 'i2of5',
  '2of5' = '2of5',
  code_93 = 'code_93'
}


@Component({
  selector: 'app-scanner',
  templateUrl: './scanner.component.html',
  styleUrls: ['./scanner.component.scss'],
})
export class ScannerComponent implements OnInit, OnDestroy, AfterViewInit {

  @ViewChild('scanner')
  private barcodeScanner: ElementRef<HTMLDivElement>;
  private configQuagga: QuaggaJSConfigObject;
  private readonly isStartingSubject$ = new BehaviorSubject(false);
  private readonly isStartedSubject$ = new BehaviorSubject(false);

  public readonly isStarting$: Observable<boolean>;
  public readonly isStarted$: Observable<boolean>;

  @Input()
  public config: Partial<QuaggaJSConfigObject>;

  @Input()
  public deviceId: string;

  @Input()
  public type: BARCODE_TYPE | BARCODE_TYPE[];

  @Output()
  public readonly scanned = new EventEmitter<QuaggaJSResultObject>();

  constructor() {
    this.isStarting$ = this.isStartingSubject$.asObservable();
    this.isStarted$ = this.isStartedSubject$.asObservable();
  }

  public async start(): Promise<void> {
    console.log('start');
    if (!this.isStartedSubject$.value && !this.isStartingSubject$.value) {
      this.isStartingSubject$.next(true);
      await this._init();
      Quagga.start();
      Quagga.onProcessed(this.detectProcessed.bind(this));
      this.isStartedSubject$.next(true);
    }
  }

  public stop() {
    console.log('stop');
    if (this.isStartedSubject$.value) {
      Quagga.stop();
      this.isStartingSubject$.next(false);
      this.isStartedSubject$.next(false);
    }
  }

  public async restart() {
    console.log('restart');
    if (this.isStartedSubject$.value) {
      this.stop();
    }
    await this.start();
  }

  public ngOnInit() {
  }

  public ngOnDestroy() {
    this.stop();
  }

  public ngAfterViewInit() {
    this.start();
  }

  private detectProcessed() {
    Quagga.offProcessed(this.detectProcessed.bind(this));
    this.isStartingSubject$.next(false);
  }

  private onDetected(result: QuaggaJSResultObject) {
    this.scanned.next(result);
  }

  private _init(): Promise<void> {
    return new Promise((resolve, reject) => {
      Quagga.onProcessed((result) => this.onProcessed(result));

      Quagga.onDetected((result) => this.onDetected(result));

      this.configQuagga = defaultsDeep({}, this.config, DEFAULT_CONFIG);

      this.configQuagga.inputStream.target = this.barcodeScanner.nativeElement;

      if (this.type) {
        this.configQuagga.decoder.readers = mapToReader(this.type);
      }

      if (this.deviceId) {
        this.configQuagga.inputStream.constraints.deviceId = this.deviceId;
      }

      Quagga.init(this.configQuagga, (err) => {
        if (err) {
          console.log(err);
          return reject(err);
        }

        resolve();
      });
    });
  }

  private onProcessed(result: QuaggaJSResultObject) {

  }

}


function mapToReader(value: string | string[]) {
  if (typeof value === 'string') {
    return [`${value}_reader`];
  } else {
    return value.map(val => `${val}_reader`);
  }
}
