import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { QuaggaJSResultObject } from '@ericblade/quagga2';
import { ViewDidLeave, ViewWillEnter } from '@ionic/angular';
import { NGXLogger } from 'ngx-logger';
import { ProductsApiService } from 'src/app/api';
import { ScannerComponent } from 'src/app/components/scanner/scanner.component';

@Component({
  selector: 'app-scan',
  templateUrl: './scan.page.html',
  styleUrls: ['./scan.page.scss'],
})
export class ScanPage implements OnInit, AfterViewInit, ViewWillEnter, ViewDidLeave {
  @ViewChild(ScannerComponent)
  private scanner: ScannerComponent;

  constructor(
    private readonly router: Router,
    private readonly productsApiService: ProductsApiService,
    private readonly logger: NGXLogger
  ) {
  }

  ngOnInit() {
  }

  public async scanned(scanResult: QuaggaJSResultObject) {
    await this.scanSuccess(scanResult.codeResult.code);
  }

  public async scanSuccess(ean: string) {

    try {
      // TODO Dedicated call for only id
      const result = await this.productsApiService.productsGetProductByEan({ ean }).toPromise();
      await this.router.navigate(['product', result.id]);
    } catch {
      await this.router.navigate(['scan-failed', ean]);
    }

    this.scanner.stop();
  }

  public ionViewDidLeave() {
    this.scanner.stop();
  }

  public ionViewWillEnter() {
    this.scanner?.start();
  }

  public ngAfterViewInit() {
    this.scanner.start();
  }
}
